@mixin responsive($breakpoint) {
    @if $breakpoint == galaxy-fold {
        @media only screen and (max-width: 280px) { @content };
    } //end small
    @if $breakpoint == iphone-5 {
        @media only screen and (max-width: 321px) { @content };
    } //end 280 x 1.15 = 321 
    @if $breakpoint == galaxy {
        @media only screen and (max-width: 361px) { @content };
    } //end 321 x 1.12 = 361  
    @if $breakpoint == iphone-x {
        @media only screen and (max-width: 376px) { @content };
    } //end 361 x 1.04 = 376 small
    @if $breakpoint == pixel-2 {
        @media only screen and (max-width: 411px) { @content };
    } //end 376 x 1.09 = 411 small
    @if $breakpoint == phone-500 {
        @media only screen and (max-width: 500px) { @content };
    } //end  411 x 1.2 = 500
    @if $breakpoint == small-phone {
        @media only screen and (max-width: 575px) { @content };
    } //end 500 x 1.15 = 574 phone
    @if $breakpoint == phone {
        @media only screen and (min-width: 574px) { @content };
    } // end 500 x 1.15 = 574 phone 
    @if $breakpoint == tablet-min {
        @media only screen and (min-width: 650px) { @content };
    } // end 574 x 1.13 = 650 tablet-min
    @if $breakpoint == tablet-max {
        @media only screen and (max-width: 768px) { @content };
    } // end 650 x 1.18 = 768 tablet
    @if $breakpoint == tablet {
        @media only screen and (min-width: 768px) { @content };
    } // end 650 x 1.18 = 768 tablet
    @if $breakpoint == tablet-plus {
        @media only screen and (min-width: 830px) { @content };
    } // end 768 x 1.08 = 830 tablet

    @if $breakpoint == big-tablet {
        @media only screen and (min-width: 992px) { @content };
    } // end 830 x 1.19 = 992 big-tablet 
    @if $breakpoint == laptop {
        @media only screen and (min-width: 1030px) { @content };
    } // end 992 x 1.038 = 1030 laptop
    @if $breakpoint == laptop-max {
        @media only screen and (max-width: 1030px) { @content };
    } // end 992 x 1.038 = 1030 laptop
    @if $breakpoint == desktop {
        @media only screen and (min-width: 1200px) { @content };
    } //end 1030 x 1.16 = 1200 
    @if $breakpoint == desktop-max {
        @media only screen and (max-width: 1200px) { @content };
    } //end 1030 x 1.16 = 1200

    @if $breakpoint == big-desktop {
        @media only screen and (min-width: 1400px) { @content };
    } // end 1200 x 1.16 = 1400
    @if $breakpoint == developer {
        @media only screen and (min-width: 1600px) { @content };
    } // end 1400 x 1.142 = 1600
    @if $breakpoint == developer-plus {
        @media only screen and (min-width: 1800px) { @content };
    } // end 1600 x 1.125 = 1800 
    @if $breakpoint == developer-standard {
        @media only screen and (min-width: 1920px) { @content };
    } // end 1920 x 1.0666 = 1920
    @if $breakpoint == tv {
        @media only screen and (min-width: 2000px) { @content };
    } // end 1920 x 1.041 = 2000
    @if $breakpoint == mid-tv {
        @media only screen and (min-width: 2250px) { @content };
    } // end 2000 x 1.125 = 2250

    @if $breakpoint == big-tv {
        @media only screen and (min-width: 2500px) { @content };
    } // end 2250 x 1.111 2500



} //end mixin

