.footer {
display:grid;
grid-template-columns:1fr; 
grid-template-rows:auto;                
min-height:150px;
width:100%;
background:$color-footer-background;
align-items:center;

@include responsive(big-tv){
grid-template-columns:1fr minmax(auto, 2500px) 1fr;
}//2500px

& > .footer_container{
grid-column:1; 
grid-row:1;
display:grid;
grid-template-columns: 1fr 1fr 1fr;
grid-template-rows:.5fr 1fr .5fr;
//min-height:150px;
height:100%;
width:100%;
//background:$color-band;
align-items:center;
padding:$pad-small;

@include responsive(pixel-2){
grid-template-rows:.5fr 1fr .5fr .5fr;
}

@include responsive(big-tv){
grid-column:2; 
}//2500px

@include text-template($large,$font-family-ads, $footer-text-color);
text-align:center;

& > .hugo {
grid-column:1/4;
grid-row:1;
height:auto;
width:100%;
margin:5px 0px;
text-align:center;
align-self:start;
}//end hugo

& > a.develop{
justify-self:center;
grid-column:2;
grid-row:2;
width:100px;
height:50px;

display:flex;
align-items:center;
justify-content:center;
margin: 0px 10px 0px 10px;


@include responsive(phone){
margin: 10px;
height:50px;
width:100px;
}
@include responsive(big-tablet){
height:60px;
width:110px;
margin: 15px 10px 15px 10px;
}

& > .footer-logo {
border-radius:$rad-normal;
height:100%;
width:auto;
transition: all .2s ease-in-out;
align-items:center;
justify-content:center;
display:flex;

& > .img {
width:100%;
height:100%;
object-fit:cover;
object-position:center center;
border-radius:$rad-normal;
}//end image

}//end footer logo picture tag

}//end develop link <a>

& > a.develop:hover{
transform: scale(1.2);
}

& > .copy{
grid-column:1/4;
grid-row:3;
margin:8px 0px;

@include responsive(pixel-2){
grid-row:4;
}
}

& > section{
display:flex;
flex-wrap:wrap;
align-content:center;
justify-content:center;

& > small{
height:auto;
width:100%;
margin:.5rem 0rem;

@include responsive(small-phone){
margin:.4rem 0rem .2rem 0rem;
}//500px x 1.15 = 574px

@include responsive(tablet){
margin:.1rem 0rem .1rem 0rem;
}//500px x 1.15 = 574px

& > a{
@include text-template($small,$font-family-ads, $color-footer-links);
text-decoration:underline;
padding: .2rem;
font-size:1.1rem;

@include responsive(small-phone){
padding: .3rem;
}//500px x 1.15 = 574px
}//a

}//end small

}//end sections

& > .footer-right{
grid-column:3;
grid-row:2;
align-items:center;

@include responsive(pixel-2){
grid-column:2;
grid-row:3;
text-align:center;
}

@include responsive(tablet){
text-align:left;
}

& > small > a:hover{
color: $color-footer-links-hover;
font-size:102%;
}//end hover
}//end footer right

}//end footer_container
}//end footer
