//article on list page
.post-summary-grid {
width:100%;
height:auto;
display: grid;
grid-template-columns: 3% 94% 3%;
grid-template-rows: 11rem 2rem 2rem minmax(3rem, auto) minmax(3.5rem, auto) minmax(auto, auto) 1rem;
border: 1px solid #ccc;
border-radius:$rad-big;
justify-content: center;
margin:0rem;
background-color:#fff;

@include responsive(small-phone){
grid-template-rows: 11rem 1.5rem 1.5rem minmax(3rem, auto) minmax(3.5rem, auto) minmax(auto, auto) 4rem 1rem;
margin-bottom:1.875rem;
}//575px

@include responsive(galaxy-fold){
grid-template-rows: 9rem 1.5rem 1.5rem minmax(3rem, auto) minmax(3.5rem, auto) minmax(auto, auto) 4rem 1rem;
}//280px

//forward cascade
@include responsive(phone){
grid-template-rows: 13rem 1.8rem 1.8rem minmax(3rem, auto) minmax(3.5rem, auto) minmax(auto, auto) 4rem 1rem;
margin-bottom:2.5rem;
}

@include responsive(tablet){
grid-template-columns: 5% 90% 5%;
grid-template-rows: 14rem 1.8rem 1.8rem minmax(3rem, auto) minmax(3.5rem, auto) minmax(auto, auto) 5rem 1rem;
}

@include responsive(desktop){
grid-template-columns: 6% 88% 6%;
//grid-template-rows: 17rem 1.8rem 1.8rem minmax(3rem, auto) minmax(3.5rem, auto) minmax(auto, auto) 5rem 1rem;
margin-bottom:3.125rem;
}

@include responsive(big-desktop){
grid-template-columns: 7% 86% 7%;
grid-template-rows: 14rem 1.8rem 1.8rem minmax(3rem, auto) minmax(3.5rem, auto) minmax(auto, auto) 5rem 1rem;
}

@include responsive(developer){
grid-template-rows: 15rem 1.8rem 1.8rem minmax(3rem, auto) minmax(3.5rem, auto) minmax(auto, auto) 5rem 1rem;
}

@include responsive(tv){
grid-template-rows: 16rem 1.8rem 1.8rem minmax(3rem, auto) minmax(3.5rem, auto) minmax(auto, auto) 5rem 1rem;
}

@include responsive(big-tv){
grid-template-rows: 17rem 1.8rem 1.8rem minmax(3rem, auto) minmax(3.5rem, auto) minmax(auto, auto) 5rem 1rem;
}

& > h2{
}//end summary-grid h2

& > .post-summary-image {
grid-column: 1 / -1;
grid-row: 1 / 3 ;
margin:0rem;
padding:0rem;

@include responsive(small-phone) {
height: 100%;
}
@include responsive(phone) {
height: 100%;
}
@include responsive(tablet) {
//height: 100%;
}
@include responsive(desktop) {
//height: 100%;
}
@include responsive(big-desktop) {
//height: 100%;
}

& > .list-img {
background-size: cover;
background-repeat: no-repeat;
background-position: center;
height: 100%;
width:auto;
background-color: #fff;
border-radius:$top-rad-big;
margin: 0px -1px 0px -1px ;
display:flex;
align-items:center;
justify-content:center;

& > .img {
width:100%;
height:100%;
object-fit:cover;
object-position:center center;
border-radius:$top-rad-big;
}//end image

}//end list image
}//end .post-summary-image

& > .post-summary-author {
grid-column:2; 
grid-row: 2 / 4 ; 
display:grid;
grid-template-columns: 1.5fr 3.5fr;
grid-template-rows:auto ;
height: 100%;
//width: 100%;
margin:0;
border-radius:$rad-big;
justify-self:start;
background:$color-primary;
border: .5px solid $color-author-border;
box-shadow: 0px 1px 1px 1px $color-author-box;
justify-content:start;
align-content: center;
height:min-content;
z-index:1;

//backward cascade
@include responsive(small-phone){
width: 40%;
}//575px

@include responsive(phone){
width: 45%;
grid-template-columns: 1.5fr 4fr;
}//500px x 1.15 = 574px

@include responsive(tablet){
width: 45%;
}//650px x 1.18 = 768px

@include responsive(big-tablet){
width: 40%;
}//650px x 1.18 = 768px

@include responsive(laptop){
box-shadow: 0px 3px 2px 2px $color-author-box;
grid-template-columns: 1.5fr 4fr;
}//650px x 1.18 = 768px

@include responsive(desktop){
width: 45%;
}//1030px x 1.16 = 1200px

@include responsive(tv){
//width:30%;
justify-content:center;
}//1400px x 1.142 = 1600px

& > .authSize{
grid-column:1;
grid-row:1;
height:auto;
width:100%;
align-self:center;
justify-self:center;
align-items: center;
//background:green;

@include responsive(small-phone){
margin:.3rem 0rem;
}//1030px

@include responsive(phone){
margin:.3rem 0rem;
}//1030px

@include responsive(laptop){
margin:.3rem 0rem;
}//1030px

@include responsive(desktop){
margin:.35rem 0rem;
}//end desktop
@include responsive(developer-plus){
margin:.38rem 0rem;
}//end developer
@include responsive(tv){
margin:.5rem 0rem;
}//end developer
@include responsive(big-tv){
margin:.7rem 0rem;
}//end developer

& > .picture {
width:100%;
height:100%;
display:flex;
align-items:center;
justify-content:center;

& > .authorImage {
width:fit-content;
width:auto;
height:2.2rem;
object-fit:contain;
object-position:center center;

@include responsive(small-phone){
height:2.6rem;
}//575px

@include responsive(galaxy){
height:2.5rem;
}//361px

@include responsive(iphone-5){
height:2.2rem;
}//321px

@include responsive(phone){
height:2.6rem;
}//end tablet

@include responsive(tablet){
height:2.6rem;
}//end tablet

@include responsive(desktop){
height:3rem;
}//end desktop

@include responsive(developer-plus){height:3.5rem;}//end developer
@include responsive(tv){height:4.3rem;}//end developer
@include responsive(mid-tv){height:4rem;}//end developer
@include responsive(big-tv){height:4.2rem;}//end developer
}//end author image
}//end picture
}//end author size

& > small {
grid-column:2;
grid-row:1;
width:100%;
height:auto;
display:grid;
align-content: center;

@include text-r($font-family-reset,$small,400, $color-author-text);

& > strong {
height:auto;
margin:0;
}//end author name

& > p {
height:auto;
margin:0;
}//end date

}//end small

}//end post-summary-author

& > .post-summary-title {
grid-column: 2;
grid-row: 4;
display:flex;
flex-wrap:wrap;
width: 100%;
height:100%;
text-align: center;
margin: 1rem 0rem 0rem 0rem;
padding: 0rem 0rem 0rem 0rem;
justify-content:center;
align-items:center;

@include responsive(phone){
margin: 1.2rem 0rem 0rem 0rem;
}//500px x 1.15 = 574px

@include responsive(tablet){
margin: 1.3rem 0rem 0rem 0rem;
}//650px x 1.18 = 768px

@include responsive(big-tablet){
margin: 1.4rem 0rem 0rem 0rem;
}//830px x 1.19 = 992px 

@include responsive(desktop){
margin: 1.4rem 0rem 0rem 0rem;
}//992px x 1.038 = 1030px

@include responsive(big-desktop){
margin: 1.5rem 0rem 0rem 0rem;
}//1200px x 1.16 = 1400px

@include responsive(developer-plus){
margin: 1.6rem 0rem 0rem 0rem;
}//1600px x 1.125 = 1800px

@include responsive(tv){
margin: 1.7rem 0rem 0rem 0rem;
}//1920px x 1.041 = 2000px

@include responsive(big-tv){
margin: 1.8rem 0rem 0rem 0rem;
}//2250px x 1.111 2500px

& > h2{
//@include text-h(font, size, weight, color);
@include text-ph($previews-blog, $massive, 600, $blog-headlines);
}//h2
}//end post summary title

& > .post-summary {
grid-column: 2;
grid-row: 5;
width: 100%;
height:auto;
justify-self: center;
text-align: center;
padding: 0.3rem 0rem 0.3rem 0rem;
margin:1rem 0rem 0rem 0rem;
overflow:hidden;

@include text-ph($summary-text,$large, 400, $color-summary-text);

@include responsive(small-phone){
font-size:$larger;
}//end small-phone

@include responsive(phone){
font-size:$big;
}//end small-phone

}//end summary

& > .post-summary-meta {
grid-column: 2;
grid-row: 6 ;
display:flex;
border: none;
justify-content:center;
align-items: flex-end;
flex-wrap:wrap; 
height: auto; 
width: 100%;

@include responsive(phone){
justify-content:center;
justify-self:start;
}//500px x 1.15 = 574px

& > .post-tags{
width: auto;
padding:.3rem .7rem .3rem .7rem;
margin: .8rem .8rem 0rem 0rem;   
background: $color-preview-tags-background;
border-radius: $rad-normal;

@include text-ph($tags-text, $big, 400, $color-preview-tags-text);
//reverse cascade

@include responsive(desktop){
font-size:$less-big;
}//2000px x 1.125 = 2250px

}//end post tags

& > .post-tags:hover{
transform: scale(1.05);
}//end .post-tags:hover

}//end post-summary-meta

& > .post-summary-continue {
grid-column: 2;
grid-row: 6;
border: none;
height: 100%; 
width: auto;
display:flex;
margin:.8rem 0rem 0rem 0rem;
justify-self:end;
align-self: flex-start;
justify-content:center;
align-items: center;

@include responsive(small-phone){
grid-row: 7;
}//575px

@include responsive(phone){
grid-row: 7;
}//575px

& > a{
width: auto;
padding:.5rem .7rem .4rem .7rem;
background: $color-preview-tags-background;
border-radius:$rad-normal;
@include text-ph($tags-text, $med, 400, $color-preview-tags-text);
}//end post tags
}// end .post-summary-continue

& > .post-summary-continue:hover {
transform: scale(1.05);
}

& > .post-summary-continue:visited, & > a.post-summary-continue:active, & > a.post-summary-continue:focus {
//border:none;
//outline:none;
}

}//end post-summary-grid

.mobile-iframe {
width: 100%;
height: 0;
padding-bottom: 56.2%;
position: relative;
}

.mobile-iframe iframe {
position: absolute;
width: 100%;
height: 100%;
}

.post h5 {
margin-bottom: 1rem;
}

.ul-news {
display: inline-block;
}

.img-zoom {
display: block;
margin: auto;
max-width: 100%;
}

.aspectRatioPlaceholder .img-zoom {
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
}

.zoom-effect .img-zoom {
cursor: pointer;
cursor: -webkit-zoom-in;
backface-visibility: hidden;
}

.zoom-effect .zoomImg,
.zoom-effect .zoomImg-wrap {
position: relative;
z-index: 900;
transition: all 300ms;
}

.zoom-effect .zoomImg-wrap--absolute {
position: absolute;
top: 0;
left: 0;
width: 100%;
height: 100%;
}

.zoom-effect .zoomImg {
cursor: pointer;
cursor: -webkit-zoom-out;
}

.zoom-effect .zoomOverlay.show {
opacity: 1;
}

.zoom-effect .zoomOverlay {
z-index: 800;
background: #fff;
position: absolute;
transition: opacity 300ms;
opacity: 0;
cursor: pointer;
cursor: -webkit-zoom-out;
}

img-end, p {
margin-block-end: 0;
margin-block-start: 0;
}

.inline-image {
float: right;
margin-left: 10px;
}
.inline-image img {
border-radius: 10px;
max-width: 100%;
height: auto;
}

//share icons
.share-icons {
font-size: $med;
}

.share-icons a {
color:$color-single-share-links; 
}

.share-icons .fa-inverse {
color:$color-share;
}

//article on search page summary
.thumb-search {
height: 5.625rem;
padding: 0.25rem;
border-radius: 0.8rem;
width: 100%;
object-fit: contain;
}

.thumb-img {
@include responsive(small-phone) {
height: 7.5rem;
}

@include responsive(phone) {
height: 7.5rem;
}

@include responsive(tablet) {
height: 5.625rem;
}

@include responsive(desktop) {
height: 300px;
}
}//end search image
