#bh-form{
& > .form-group{
margin-bottom: .8rem;
display:flex;
flex-wrap:wrap;
& > .form-control {
display: block;
width: 100%;
height:auto;
padding: .375rem .75rem;
background-color: #fff;
background-clip: padding-box;
border: 1px solid #ced4da;
border-radius: .25rem;
line-height: 1.5;
@include text-ph($font-family-base, $normal, 400, $color-form-control);
transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}//end form control
& > .bh-btn {
color: #fff;
background-color:$bh-button-color; 
border: 1px solid transparent;
border-color: $bh-button-color;
width: auto;

padding: 1rem 1.4rem .8rem 1.4rem;
margin: 1.2rem 0rem 1.2rem 0rem;
border-radius:.25rem;

@include text-ph($bh-button-font, $normal-plus, 400, $color-preview-tags-text);

text-align:center;
transition: all .2s ease-in-out;
line-height: 1.5;
user-select:none;
vertical-align: middle;
display: inline-block;

@include responsive(small-phone){
padding: .8rem 1.2rem .6rem 1.2rem;
font-size:$big-plus;
}//575px

@include responsive(phone){
padding: .8rem 1.2rem .6rem 1.2rem;
font-size:$big-plus;
}//575px

@include responsive(laptop){
padding: .5rem .7rem .4rem .7rem;
}//575px
}//end bh-btn
& > .bh-btn:hover{
transform: scale(1.1);
}
& > input{
margin-top:0px;
}//end input
}//end .form-group
}//end bh-form
.blog-heading{
padding:0rem;
margin-bottom:.5rem;
width:100%;
text-align:center;

@include text-ph($title-blog, $massive, 600, $blog-headlines);

@include responsive(desktop){
margin-bottom:1rem;
}//end desktop

@include responsive(tv){
margin-bottom:1.3rem;
}//end developer
}//end .blog-heading
.p-news{
width:100%;
height:auto;
@include text-ph($font-family-arimo, $jumbo, 400, $color-p-news);
}//end p-news

@include responsive(phone){
font-size:$big;
}//end small-phone
#disqus_thread{
width: 100%;
max-width: 800px;
//reverse cascade
@include responsive(small-phone){
margin-top: 2rem;
}//500px x 1.15 = 574px
//forward cascade
@include responsive(tablet){
margin-top: 3rem;
}//650px x 1.18 = 768px
@include responsive(desktop){
margin-top: 4rem;
}//1030px x 1.16 = 1200px
@include responsive(developer){
margin-top: 4.5rem;
}//1400px x 1.142 = 1600px
@include responsive(tv){
margin-top: 5rem;
}//1920px x 1.041 = 2000px

& > iframe > iframe:nth-child(1) {
height:none !important;  
display:none !important;
}//disqus ads

& > iframe:nth-child(3){
height:none !important;  
display:none !important;
}

}//disqus ads
.form-text{
margin:.4rem 0rem .8rem .8rem;
@include text-ph($font-family-base, $small, 400, $color-form-control);
//$color-p-news
}
.home-btn {
color: #fff;
background-color:$bh-button-color; 
border: 1px solid transparent;
border-color: $bh-button-color;
width: auto;
//padding: .5rem .7rem .4rem .7rem;
//@include padding-r(1rem, 1.4rem, .8rem, 1.4rem);
padding: 1rem 1.4rem .8rem 1.4rem;
margin: 1.2rem 0rem 1.2rem 0rem;
border-radius: .25rem .25rem .25rem .25rem;
@include text-ph($bh-button-font, $normal-plus, 400, $color-preview-tags-text);

text-align:center;
transition: all .2s ease-in-out;
line-height: 1.5;
user-select:none;
vertical-align: middle;
display: inline-block;

@include responsive(small-phone){
padding: .8rem 1.2rem .6rem 1.2rem;
font-size:$big-plus;
//color:red;
}//575px

@include responsive(phone){
padding: .8rem 1.2rem .6rem 1.2rem;
font-size:$big-plus;
//color:red;
}//575px

@include responsive(laptop){
padding: 1rem 1.4rem .8rem 1.4rem;
}//575px

}//end bh-btn
.home-btn:hover{
transform: scale(1.1);
}
.page-link{
padding: .45rem 1rem;
}
.preview_loops, .post-search-grid {
display: grid;
grid-template-columns: 1fr;
grid-template-rows: 9.5rem .1fr minmax(5rem, auto);
border: 1px solid #ccc;
border-radius: $rad-big;
justify-content: center;
 
@include responsive(small-phone){
grid-template-rows: 9.5rem .1fr minmax(5rem, auto);
margin:0rem 0rem 1.875rem 0rem;
}//575px

@include responsive(phone){
margin:0rem 0rem 2.5rem 0rem;
}

@include responsive(tablet){
grid-template-rows: 6rem .1fr minmax(5rem, auto);
}

@include responsive(desktop){
grid-template-rows: 6.5rem .1fr minmax(5rem, auto);
margin:0rem 0rem 3.125rem 0rem;
}//end desktop

@include responsive(big-desktop){
grid-template-rows: 7rem .1fr minmax(5rem, auto);
}

@include responsive(developer){
grid-template-rows: 6.5rem .1fr minmax(5rem, auto);
}

@include responsive(tv){
grid-template-rows: 7rem .1fr minmax(5rem, auto);
}
& > .side-preview-image {
grid-column:1;
grid-row:1;
height:100%;
width:auto;
display:flex;
background-size: cover;
background-repeat: no-repeat;
background-position: center;
border-radius:$top-rad-big;
align-items:center;
justify-content:center;
margin:0px;
& > .img {
width:100%;
height:100%;
object-fit:cover;
object-position:center center;
border-radius:$top-rad-big;
}//end image
}//end .side-preview-image
& > h3 {
height:100%;
width:100%;
grid-column:1;
grid-row:3;
@include text-ph($previews-blog,$massive, 600, $blog-headlines);
justify-self:center;
align-self:center;
text-align:center;

@include responsive(small-phone){
padding: .3rem .5rem 1rem .5rem;
}//575px

@include responsive(galaxy){
padding: .3rem .3rem 1rem .3rem;
}//361px

@include responsive(phone){
padding: .6rem .6rem 1rem .6rem;
}//280px

@include responsive(tablet){
font-size:$normal-plus;
}//650px x 1.18 = 768px

@include responsive(laptop){
font-size:$larger;
}//650px x 1.18 = 768px

@include responsive(desktop) {
align-self:start;
padding: .2rem .2rem .3rem .2rem;
font-size:$normal;
}

@include responsive(developer) {
align-self:start;
padding: .6rem .6rem 1rem .6rem;
}

@include responsive(tv){
padding: 1rem 1rem 1.3rem 1rem;
}//1920px x 1.041 = 2000px


& > span > .strikethrough {
position: relative;
white-space: nowrap; /* would center line-through in the middle of the wrapped lines */
//color:red;
}

& > span > .strikethrough:after {
border-top: 3px solid red;
position: absolute;
content: "";
right: 0;
top:45%;
left: 0;
}

}//end h3
& > p {
grid-column:1;
grid-row:2;
justify-self:start;
align-self:flex-start;
padding: .8rem;
@include text-ph( $font-family-base, $big, 400, $color-preview-date)

@include responsive(phone){
font-size:$smallish;
}//575px

}//end date
}//end preview loop
.preview_loops:hover {
@include responsive(laptop) {
//@include background(none,$color-primary, 1, cover);
transform: scale(1.02);
font-size:100.2%;
color:$color-blog-previews-hover;
//box-shadow: 0 48px 48px 5px rgba(0,0,0,1);
box-shadow: 0 36px 36px 5px rgba(0,0,0,1);
}//end at include laptop
}//end hover preview-loops (individual blog posts)
.side-content {
display:flex;
align-content:flex-start;
justify-items:center;
flex-wrap:wrap;
padding: 0rem;
word-break:break-word;

@include responsive(small-phone){
padding: 0px 10px;
padding: 0px 16px;
}//575px

@include responsive(phone){
padding: 0px 15px 20px 15px;
}//280px
& > div {
width:100%;
margin-bottom:20px;

@include responsive(small-phone){
margin-bottom:10px;
}//575px

@include responsive(galaxy){
margin-bottom:5px;
}
& > h2{
padding:0rem;
margin-bottom:.5rem;
width:100%;
text-align:center;
@include text-ph($title-blog, $supersized, 600, $blog-headlines);

@include responsive(phone){
font-size: $titanic;// 6rem
}//500px x 1.15 = 574px

@include responsive(tablet){
line-height:1;
font-size: $larger;
}//650px x 1.18 = 768px

@include responsive(big-tablet){
line-height:1.3;
font-size: $jumbo;//2rem
}//650px x 1.18 = 768px

@include responsive(desktop){
line-height:1;
font-size:$big;
margin-bottom:1rem;
}//end desktop

}//end h2
& > p{
width:100%;
margin-bottom:15px;

@include text-ph($sidebar-text,$large, 400, $color-preview-text);

@include responsive(phone){
font-size:$big;
}//end small-phone

}//end p tag
}//end div
}//end side content
.post-section-heading{
@include text-ph($title-blog, $massive, 600, $blog-headlines);
margin:1.5rem 0rem 1.5rem 0rem;
width: 100%;
height:min-content;
text-align:center;
}
h1{
@include text-h($font-family-base, $larger, 600, $color-grayish);
}
h2{
@include text-h($font-family-base, $large, 600, $color-grayish);
}
h3{
@include text-h($font-family-base, $med, 600, $color-grayish);
}//end h3
h4{
@include text-h($font-family-base, $big, 600, $color-grayish);
}//end h4
h5{
@include text-h($font-family-base, $less-big, 600, $color-grayish);
}//end h5
