.main{
width:100%;
height:min-content;
min-height:100vh;
display:flex;
flex-wrap:wrap; 
align-content:flex-start;
justify-content:flex-start; 
margin:0px;
padding:1.5rem 0rem 0rem 0rem;
overflow:hidden;

& > .banner-title{
width:100%;
height:100%;
display:flex;
justify-content:center;
align-content:center;

& > .post-section-heading{
@include text-ph($font-family-nordique,$larger,600, $blog-headlines);
}

}//.banner-title

& > .posts{
display:flex;
flex-wrap:wrap; 
align-content:flex-start;
justify-content:flex-center;
flex-direction: row; 
margin:0px;
height:100%;
width:100%;
padding: 1.5rem 0rem 1rem 0rem;

& > div{
width:100%;
display:flex;
align-content:center;
justify-content:center;
flex-wrap:wrap;
flex-direction: row;

//.pagination
& > ul{
display:flex;
flex-direction: row;
flex-wrap:wrap;
margin-bottom:20px;
align-items:center;
width:100%;
justify-content:center;

& > .page-item.active{ 
//height: 2.2rem;

& > .page-link {
z-index: 1;
color: #fff;
background-color: rgba(39, 52, 60, 0.9);
background-color:transparent;
border-color: transparent;
//font-family:$tags-text;
}

}//page-item.active

& > .page-item.disabled{
//height: 2.04rem;

> .page-link {
color: #515151;
pointer-events: none;
cursor: auto;
border-color: #dee2e6;
}

& > span{
display:table-cell;
align-self:end;
vertical-align:middle;
height:100%;
text-align: center;
}

}//end page-item.disabled

& > .page-item.disabled.hellip{
//float: left;
line-height: 1.875rem;
text-decoration: none;
border: 1px solid #ddd;
border-left-width: 0;

@include responsive(small-phone){
font-size:1.2rem;
padding: .32rem .8rem;
}//575px
@include responsive(iphone-x){
font-size:1.2rem;
padding: .31rem .7rem;

}//376px
@include responsive(galaxy){
font-size:1.1rem;
padding: .30rem .7rem;
}//361px
@include responsive(iphone-5){
padding: .30rem .45rem;
font-size:.9rem;
}//376px
@include responsive(galaxy-fold){}//280px

@include responsive(phone){
font-size:1.2rem;
padding: .35rem .8rem;
}//280px

}//end hellip

& > li{
& > .page-link {
color: #515151;

@include responsive(small-phone){
font-size:1.2rem;
padding: .32rem .8rem;
}//575px
@include responsive(iphone-x){
font-size:1.2rem;
padding: .31rem .7rem;

}//376px
@include responsive(galaxy){
font-size:1.1rem;
padding: .30rem .7rem;
}//361px
@include responsive(iphone-5){
padding: .30rem .45rem;
font-size:.9rem;
}//376px
@include responsive(galaxy-fold){}//280px

@include responsive(phone){
font-size:1.2rem;
padding: .35rem .8rem;
}//280px

//font-family:$tags-text;
}//page-link

}//end li

}//end ul
}//end div

}//end posts

}//end main

.pagination{
height:3rem;
margin:0;
padding: 0;
}

.pager,.pagination ul{
margin-left:0;
*zoom:1;
}
.pagination ul{
padding:0;
margin-bottom:0;
-webkit-border-radius:3px;
-moz-border-radius:3px;
border-radius:3px;
-webkit-box-shadow:0 1px 2px rgba(0,0,0,.05);
-moz-box-shadow:0 1px 2px rgba(0,0,0,.05);
box-shadow:0 1px 2px rgba(0,0,0,.05);
}

.pagination li{
display:inline;
}

.pagination a{
float:left;
padding:0 12px;
line-height:1.875rem;
text-decoration:none;
border:1px solid #ddd;
border-left-width:0;
}
.pagination .active a,.pagination a:hover{
background-color:#f5f5f5;
color:#94999E;
}
.pagination .active a{
color:#94999E;
cursor:default;
}
.pagination .disabled a,
.pagination .disabled a:hover,
.pagination .disabled span{
color:#94999E;
background-color:transparent;
cursor:default;
}
.pagination li:first-child a,
.pagination li:first-child span{
border-left-width:1px;
-webkit-border-radius:3px 0 0 3px;
-moz-border-radius:3px 0 0 3px;
border-radius:3px 0 0 3px;
}
.pagination li:last-child a{
-webkit-border-radius:0 3px 3px 0;
-moz-border-radius:0 3px 3px 0;
border-radius:0 3px 3px 0;
}
.pagination-centered{
text-align:center;
}
.pagination-right{
text-align:right;
}

.pagination .prev.disabled span{
float:left;
padding:0 12px;
line-height:1.875rem;
text-decoration:none;
border:1px solid #ddd;
border-left-width:1;
}
.pagination .next.disabled span{
float:left;
padding:0 12px;
line-height:1.875rem;
text-decoration:none;
border:1px solid #ddd;
border-left-width:0;
}
.pagination li.active, 
.pagination li.disabled {
//float:left;
//padding:0 12px;
line-height:1.875rem;
text-decoration:none;
//border:1px solid #ddd;
